export const TMDB_API_KEY = process.env.REACT_APP_TMDB_API_KEY;
export const TMDB_API_URL = process.env.REACT_APP_TMDB_API_URL;

export interface IMovie {
    id: number;
    title: string;
    overview: string;
    backdrop_path: string;
    poster_path: string;
}
export interface IMoviesResult {
    dates: {
        maximum: string;
        "minimum": string;
    };
    page: number;
    results: IMovie[];
    total_pages:number;
    total_results:number;
}

export async function getMovies() {
    return (await fetch(`${TMDB_API_URL}/movie/now_playing?api_key=${TMDB_API_KEY}`)).json();
}